<template>
  <div>
    <div class="sub-header">
      <router-link v-if="!isLogined" to="/Login" class="font-link">로그인</router-link>
      <router-link v-else to="/" @click="logout" class="font-link">로그아웃</router-link>

      <a href="https://www.instagram.com/" target="_blank" rel="noopener noreferrer" class="social-link">
        <img src="@/assets/images/instagram.png" alt="" class="social-icon" />
