</template>

<script>

export default {
  data() {
    return {
      isLogined: sessionStorage.getItem("isLogined") === "true",
    };
  },
    watch: {
    isLogined(newValue) {
      console.log("isLogined changed:", newValue);
    },
  },
  methods: {
    logout() {
      sessionStorage.setItem("isLogined", "false");
      this.isLogined = false;
      console.log("logout");
    },
  },
};
</script>

