import { createRouter, createWebHashHistory } from 'vue-router';
import About from "../components/contents/AboutContent";
import SkillsContent from "../components/contents/SkillsContent";
import ProjectContent from "../components/contents/ProjectContent";
import GoalContent from "../components/contents/GoalContent";
import LoginContent from "../components/login/LoginContent";
import RegistContent from "../components/login/RegistContent";

const routes = [
  { path: "/", component: About},
  { path: "/SkillsContent", component: SkillsContent},
  { path: "/ProjectContent", component: ProjectContent},
  { path: "/GoalContent", component: GoalContent},
  { path: "/Login", component: LoginContent},
  { path: "/Regist", component: RegistContent},

];


const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;